"use client"

import { useState, useEffect, use } from "react"
import { Card, CardContent, CardHeader } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Textarea } from "@/components/ui/textarea"
import {
  getBlogPostById,
  getPetById,
  getUserById,
  getCommentsByPostId,
  addComment,
  updateComment,
  deleteComment,
  updateBlogPost,
  toggleCommentReaction,
} from "@/lib/storage"
import { useAuth } from "@/lib/auth"
import { Heart, MessageCircle, ArrowLeft, Send, Reply, Edit2, Trash2, X, Check, Smile } from "lucide-react"
import Link from "next/link"
import type { Comment, ReactionType } from "@/lib/types"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"

export default function BlogPostPage({ params }: { params: Promise<{ id: string }> }) {
  const { id } = use(params)
  const { user: currentUser } = useAuth()
  const [post, setPost] = useState(() => getBlogPostById(id))
  const [pet, setPet] = useState(() => (post ? getPetById(post.petId) : null))
  const [author, setAuthor] = useState(() => (post ? getUserById(post.authorId) : null))
  const [comments, setComments] = useState<Comment[]>(() => (post ? getCommentsByPostId(post.id) : []))
  const [hasLiked, setHasLiked] = useState(false)
  const [newComment, setNewComment] = useState("")
  const [replyingTo, setReplyingTo] = useState<string | null>(null)
  const [replyContent, setReplyContent] = useState("")
  const [editingCommentId, setEditingCommentId] = useState<string | null>(null)
  const [editContent, setEditContent] = useState("")

  useEffect(() => {
    if (post && currentUser) {
      setHasLiked(post.likes.includes(currentUser.id))
    }
  }, [post, currentUser])

  const handleLike = () => {
    if (!currentUser || !post) return

    const updatedPost = { ...post }

    if (hasLiked) {
      updatedPost.likes = updatedPost.likes.filter((id) => id !== currentUser.id)
    } else {
      updatedPost.likes.push(currentUser.id)
    }

    updateBlogPost(updatedPost)
    setPost(updatedPost)
    setHasLiked(!hasLiked)
  }

  const handleAddComment = () => {
    if (!currentUser || !post || !newComment.trim()) return

    const comment: Comment = {
      id: String(Date.now()),
      postId: post.id,
      userId: currentUser.id,
      content: newComment.trim(),
      createdAt: new Date().toISOString(),
      reactions: {
        like: [],
        love: [],
        laugh: [],
        wow: [],
        sad: [],
        angry: [],
      },
    }

    addComment(comment)
    const updatedComments = getCommentsByPostId(post.id)
    setComments(updatedComments)
    setNewComment("")
  }

  const handleReply = (parentCommentId: string) => {
    if (!currentUser || !post || !replyContent.trim()) return

    const reply: Comment = {
      id: String(Date.now()),
      postId: post.id,
      userId: currentUser.id,
      content: replyContent.trim(),
      createdAt: new Date().toISOString(),
      parentCommentId,
      reactions: {
        like: [],
        love: [],
        laugh: [],
        wow: [],
        sad: [],
        angry: [],
      },
    }

    addComment(reply)
    const updatedComments = getCommentsByPostId(post.id)
    setComments(updatedComments)
    setReplyContent("")
    setReplyingTo(null)
  }

  const handleStartEdit = (comment: Comment) => {
    setEditingCommentId(comment.id)
    setEditContent(comment.content)
  }

  const handleSaveEdit = (commentId: string) => {
    if (!editContent.trim()) return
    updateComment(commentId, editContent.trim())
    const updatedComments = getCommentsByPostId(post.id)
    setComments(updatedComments)
    setEditingCommentId(null)
    setEditContent("")
  }

  const handleDelete = (commentId: string) => {
    if (window.confirm("Are you sure you want to delete this comment?")) {
      deleteComment(commentId)
      const updatedComments = getCommentsByPostId(post.id)
      setComments(updatedComments)
    }
  }

  const handleReaction = (commentId: string, reactionType: ReactionType) => {
    if (!currentUser) return
    toggleCommentReaction(commentId, currentUser.id, reactionType)
    const updatedComments = getCommentsByPostId(post.id)
    setComments(updatedComments)
  }

  const getUserReaction = (comment: Comment): ReactionType | null => {
    if (!currentUser || !comment.reactions) return null
    for (const [type, userIds] of Object.entries(comment.reactions)) {
      if (userIds.includes(currentUser.id)) {
        return type as ReactionType
      }
    }
    return null
  }

  const getTotalReactions = (comment: Comment): number => {
    if (!comment.reactions) return 0
    return Object.values(comment.reactions).reduce((sum, arr) => sum + arr.length, 0)
  }

  // Organize comments into parent and replies
  const topLevelComments = comments.filter((c) => !c.parentCommentId)
  const getReplies = (commentId: string) => {
    return comments.filter((c) => c.parentCommentId === commentId)
  }

  const totalCommentsCount = comments.length

  const reactionEmojis: Record<ReactionType, string> = {
    like: "üëç",
    love: "‚ù§Ô∏è",
    laugh: "üòÑ",
    wow: "üòÆ",
    sad: "üò¢",
    angry: "üò°",
  }

  if (!post || !pet || !author) {
    return (
      <div className="container mx-auto px-4 py-8">
        <p className="text-center text-muted-foreground">Blog post not found</p>
      </div>
    )
  }

  return (
    <div className="container mx-auto px-4 py-8 max-w-7xl">
      <Link href="/blog">
        <Button variant="ghost" className="mb-6">
          <ArrowLeft className="h-4 w-4 mr-2" />
          Back to Blogs
        </Button>
      </Link>

      <Card className="overflow-hidden p-0">
        {post.coverImage && (
          <div className="aspect-video w-full overflow-hidden">
            <img src={post.coverImage || "/placeholder.svg"} alt={post.title} className="w-full h-full object-cover" />
          </div>
        )}
        <CardHeader className="space-y-4 px-6 pt-6">
          <div className="flex items-center gap-3">
            <Link href={`/pet/${pet.id}`}>
              <Avatar className="h-12 w-12 cursor-pointer">
                <AvatarImage src={pet.avatar || "/placeholder.svg"} alt={pet.name} />
                <AvatarFallback>{pet.name.charAt(0)}</AvatarFallback>
              </Avatar>
            </Link>
            <div className="flex-1">
              <Link href={`/pet/${pet.id}`} className="font-semibold hover:underline">
                {pet.name}
              </Link>
              <p className="text-sm text-muted-foreground">
                by{" "}
                <Link href={`/profile/${author.username}`} className="hover:underline">
                  {author.fullName}
                </Link>{" "}
                ‚Ä¢{" "}
                {new Date(post.createdAt).toLocaleDateString("en-GB", {
                  month: "long",
                  day: "numeric",
                  year: "numeric",
                })}
              </p>
            </div>
          </div>
          <h1 className="text-4xl font-bold leading-tight">{post.title}</h1>
          <div className="flex flex-wrap gap-2">
            {post.tags.map((tag) => (
              <Badge key={tag} variant="secondary">
                {tag}
              </Badge>
            ))}
          </div>
          <div className="flex items-center gap-2">
            <Button onClick={handleLike} variant={hasLiked ? "default" : "outline"} size="sm">
              <Heart className={`h-4 w-4 mr-2 ${hasLiked ? "fill-current" : ""}`} />
              {post.likes.length} {post.likes.length === 1 ? "Like" : "Likes"}
            </Button>
            <Button variant="outline" size="sm">
              <MessageCircle className="h-4 w-4 mr-2" />
              {totalCommentsCount} {totalCommentsCount === 1 ? "Comment" : "Comments"}
            </Button>
          </div>
        </CardHeader>
        <CardContent className="prose prose-lg max-w-none px-6 pb-6">
          <p className="text-foreground leading-relaxed whitespace-pre-wrap">{post.content}</p>
        </CardContent>
      </Card>

      {/* Comments Section */}
      <Card className="mt-6">
        <CardHeader>
          <h2 className="text-2xl font-bold">Comments</h2>
        </CardHeader>
        <CardContent className="space-y-6">
          {/* Add Comment */}
          {currentUser && (
            <div className="flex gap-3">
              <Avatar className="h-10 w-10">
                <AvatarImage src={currentUser.avatar || "/placeholder.svg"} alt={currentUser.fullName} />
                <AvatarFallback>{currentUser.fullName.charAt(0)}</AvatarFallback>
              </Avatar>
              <div className="flex-1 space-y-2">
                <Textarea
                  placeholder="Write a comment..."
                  value={newComment}
                  onChange={(e) => setNewComment(e.target.value)}
                  rows={3}
                />
                <Button onClick={handleAddComment} disabled={!newComment.trim()}>
                  <Send className="h-4 w-4 mr-2" />
                  Post Comment
                </Button>
              </div>
            </div>
          )}

          {/* Comments List */}
          <div className="space-y-4">
            {topLevelComments.map((comment) => {
              const commentUser = getUserById(comment.userId)
              const replies = getReplies(comment.id)
              const isOwner = currentUser?.id === comment.userId
              const isEditing = editingCommentId === comment.id

              return (
                <div key={comment.id}>
                  <div className="flex gap-3">
                    <Link href={`/profile/${commentUser?.username}`}>
                      <Avatar className="h-10 w-10">
                        <AvatarImage src={commentUser?.avatar || "/placeholder.svg"} alt={commentUser?.fullName} />
                        <AvatarFallback>{commentUser?.fullName.charAt(0)}</AvatarFallback>
                      </Avatar>
                    </Link>
                    <div className="flex-1">
                      <div className="bg-muted rounded-lg p-3">
                        <div className="flex items-start justify-between mb-2">
                          <div className="flex items-center gap-2">
                            <Link
                              href={`/profile/${commentUser?.username}`}
                              className="font-semibold text-sm hover:underline"
                            >
                              {commentUser?.fullName}
                            </Link>
                            {post.authorId === comment.userId && (
                              <Badge variant="default" className="text-xs">
                                Author
                              </Badge>
                            )}
                          </div>
                          {isOwner && !isEditing && (
                            <DropdownMenu>
                              <DropdownMenuTrigger asChild>
                                <Button variant="ghost" size="icon" className="h-6 w-6">
                                  <MessageCircle className="h-4 w-4" />
                                </Button>
                              </DropdownMenuTrigger>
                              <DropdownMenuContent align="end">
                                <DropdownMenuItem onClick={() => handleStartEdit(comment)}>
                                  <Edit2 className="h-4 w-4 mr-2" />
                                  Edit
                                </DropdownMenuItem>
                                <DropdownMenuItem onClick={() => handleDelete(comment.id)} className="text-destructive">
                                  <Trash2 className="h-4 w-4 mr-2" />
                                  Delete
                                </DropdownMenuItem>
                              </DropdownMenuContent>
                            </DropdownMenu>
                          )}
                        </div>
                        {isEditing ? (
                          <div className="space-y-2">
                            <Textarea
                              value={editContent}
                              onChange={(e) => setEditContent(e.target.value)}
                              rows={3}
                              className="bg-background"
                            />
                            <div className="flex gap-2">
                              <Button size="sm" onClick={() => handleSaveEdit(comment.id)}>
                                <Check className="h-4 w-4 mr-1" />
                                Save
                              </Button>
                              <Button
                                size="sm"
                                variant="outline"
                                onClick={() => {
                                  setEditingCommentId(null)
                                  setEditContent("")
                                }}
                              >
                                <X className="h-4 w-4 mr-1" />
                                Cancel
                              </Button>
                            </div>
                          </div>
                        ) : (
                          <p className="text-sm mt-1">{comment.content}</p>
                        )}
                      </div>
                      <div className="flex items-center gap-3 mt-1">
                        <p className="text-xs text-muted-foreground">
                          {new Date(comment.createdAt).toLocaleDateString("en-GB", {
                            month: "short",
                            day: "numeric",
                            year: "numeric",
                            hour: "numeric",
                            minute: "2-digit",
                          })}
                          {comment.updatedAt && " (edited)"}
                        </p>
                        {currentUser && !isEditing && (
                          <Button
                            variant="ghost"
                            size="sm"
                            className="h-6 text-xs"
                            onClick={() => {
                              setReplyingTo(replyingTo === comment.id ? null : comment.id)
                              setReplyContent("")
                            }}
                          >
                            <Reply className="h-3 w-3 mr-1" />
                            Reply
                          </Button>
                        )}
                      </div>

                      {/* Reply Input */}
                      {replyingTo === comment.id && currentUser && (
                        <div className="mt-3 ml-4 space-y-2">
                          <Textarea
                            placeholder={`Reply to ${commentUser?.fullName}...`}
                            value={replyContent}
                            onChange={(e) => setReplyContent(e.target.value)}
                            rows={2}
                            className="bg-background"
                          />
                          <div className="flex gap-2">
                            <Button size="sm" onClick={() => handleReply(comment.id)} disabled={!replyContent.trim()}>
                              <Send className="h-3 w-3 mr-1" />
                              Reply
                            </Button>
                            <Button
                              size="sm"
                              variant="outline"
                              onClick={() => {
                                setReplyingTo(null)
                                setReplyContent("")
                              }}
                            >
                              Cancel
                            </Button>
                          </div>
                        </div>
                      )}

                      {/* Replies */}
                      {replies.length > 0 && (
                        <div className="mt-3 ml-4 space-y-3 border-l-2 border-muted pl-4">
                          {replies.map((reply) => {
                            const replyUser = getUserById(reply.userId)
                            const isReplyOwner = currentUser?.id === reply.userId
                            const isEditingReply = editingCommentId === reply.id

                            return (
                              <div key={reply.id} className="flex gap-3">
                                <Link href={`/profile/${replyUser?.username}`}>
                                  <Avatar className="h-8 w-8">
                                    <AvatarImage
                                      src={replyUser?.avatar || "/placeholder.svg"}
                                      alt={replyUser?.fullName}
                                    />
                                    <AvatarFallback>{replyUser?.fullName.charAt(0)}</AvatarFallback>
                                  </Avatar>
                                </Link>
                                <div className="flex-1">
                                  <div className="bg-muted rounded-lg p-3">
                                    <div className="flex items-start justify-between mb-2">
                                      <div className="flex items-center gap-2">
                                        <Link
                                          href={`/profile/${replyUser?.username}`}
                                          className="font-semibold text-xs hover:underline"
                                        >
                                          {replyUser?.fullName}
                                        </Link>
                                        <span className="text-xs text-muted-foreground">replied to</span>
                                        <Link
                                          href={`/profile/${commentUser?.username}`}
                                          className="text-xs font-medium hover:underline"
                                        >
                                          {commentUser?.fullName}
                                        </Link>
                                      </div>
                                      {isReplyOwner && !isEditingReply && (
                                        <DropdownMenu>
                                          <DropdownMenuTrigger asChild>
                                            <Button variant="ghost" size="icon" className="h-6 w-6">
                                              <MessageCircle className="h-3 w-3" />
                                            </Button>
                                          </DropdownMenuTrigger>
                                          <DropdownMenuContent align="end">
                                            <DropdownMenuItem onClick={() => handleStartEdit(reply)}>
                                              <Edit2 className="h-4 w-4 mr-2" />
                                              Edit
                                            </DropdownMenuItem>
                                            <DropdownMenuItem
                                              onClick={() => handleDelete(reply.id)}
                                              className="text-destructive"
                                            >
                                              <Trash2 className="h-4 w-4 mr-2" />
                                              Delete
                                            </DropdownMenuItem>
                                          </DropdownMenuContent>
                                        </DropdownMenu>
                                      )}
                                    </div>
                                    {isEditingReply ? (
                                      <div className="space-y-2">
                                        <Textarea
                                          value={editContent}
                                          onChange={(e) => setEditContent(e.target.value)}
                                          rows={2}
                                          className="bg-background text-sm"
                                        />
                                        <div className="flex gap-2">
                                          <Button size="sm" onClick={() => handleSaveEdit(reply.id)}>
                                            <Check className="h-3 w-3 mr-1" />
                                            Save
                                          </Button>
                                          <Button
                                            size="sm"
                                            variant="outline"
                                            onClick={() => {
                                              setEditingCommentId(null)
                                              setEditContent("")
                                            }}
                                          >
                                            <X className="h-3 w-3 mr-1" />
                                            Cancel
                                          </Button>
                                        </div>
                                      </div>
                                    ) : (
                                      <p className="text-sm mt-1">{reply.content}</p>
                                    )}
                                  </div>
                                  <p className="text-xs text-muted-foreground mt-1">
                                    {new Date(reply.createdAt).toLocaleDateString("en-GB", {
                                      month: "short",
                                      day: "numeric",
                                      year: "numeric",
                                      hour: "numeric",
                                      minute: "2-digit",
                                    })}
                                    {reply.updatedAt && " (edited)"}
                                  </p>
                                </div>
                              </div>
                            )
                          })}
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              )
            })}
          </div>

          {topLevelComments.length === 0 && !currentUser && (
            <p className="text-center text-muted-foreground py-8">No comments yet. Be the first to comment!</p>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
